import React, { useEffect, useState } from "react"
import { useInView } from "react-intersection-observer"

const Reveal = ({ children, animation, delay }) => {
  const [animationStart, setAnimationStart] = useState(false)
  const { ref, inView } = useInView()

  useEffect(() => {
    let timer
    if (inView) {
      timer = setTimeout(() => setAnimationStart(true), delay || 0)
    } else {
      setAnimationStart(false)
    }
    return () => inView && clearTimeout(timer)
  }, [inView, delay])

  return (
    <div
      ref={ref}
      className={animationStart ? `animate-${animation}` : "opacity-0"}
    >
      {children}
    </div>
  )
}

export default Reveal

export const delayCalc = (start, step) =>
  (() => {
    let i = 0
    return () => start + step * i++
  })()
